import React from 'react';

const Task = props => {
  const style = {color: '#B23121'}

  const { text, date, id, active,important, finishDate } = props.task;
  if(active){
    return (
      <>
        <div className="task">
          <strong style={important ? style : null}>{text}</strong> - do <span>{date} </span>
          <button onClick={()=>props.change(id)} className="completeButton">Zrobione</button><button onClick={()=>props.delete(id)} className="xButton">X</button>
        </div>


      </>
    );
  } else {
    const finish = new Date(finishDate).toLocaleString()
    return (
      <p>
        <strong style={important ? style : null}>{text}</strong> - do <em>(Zrobić do {date}) - Wykonano -{finish} </em>
        <button onClick={() => props.delete(id)}>X</button>
      </p>
    );
  }
}

export default Task;