import React,{Component} from 'react';
import "./AddTask.css";

class AddTask extends Component {
  minDate = new Date().toISOString().slice(0, 10);
  state = {
    text: "",
    checked: false,
    date: this.minDate,
  };

  handleDate = e => {
    this.setState({
      date: e.target.value
    })
  }
  handleText = e => {
    this.setState({
      text:e.target.value
    })
  }
  handleCheckbox = e => {
    this.setState({
      checked: e.target.checked
    })
    if (this.state.checked === false) {
      document.querySelector('input[type=checkbox]').classList.add("checkboxLight")
    } else {
      document.querySelector('input[type=checkbox]').classList.remove("checkboxLight")
    }
  }

  handleClick = () => {

    const { text, date, checked } = this.state
    if(text.length > 2 ){
    const add = this.props.add(text, date, checked);
    if (add) {
      this.setState({
        text: "",
        checked: false,
        date: this.minDate,
      });
    }
    }else alert('Za mało znaków')
  }

  render() {
    let maxDate = this.minDate.slice(0, 4) * 1 + 1;
    maxDate = maxDate + "-12-31";


    return (
      <div className="form">
        <h1>Add task</h1>
        <input
          onChange={this.handleText}
          type="text"
          value={this.state.text}
          placeholder="Task..."
        ></input>
        <input
          id="important"
          type="checkbox"
          checked={this.state.checked}
          onClick={this.handleCheckbox}
        ></input>
        <label htmlFor="important">Priorytetowe</label>
        <br />
        <input
          type="date"
          value={this.state.date}
          min={this.minDate}
          max={maxDate}
          onChange={this.handleDate}
        ></input>
        <button className="addTask" onClick={this.handleClick}>DODAJ</button>
      </div>
    );
  }
}

export default AddTask;